form {
  max-width: 300px;
  margin: 20px auto 300px;
}

input[type="text"] {
  border: 1px solid rgb(33, 108, 42);
  border-radius: 4px;
  background-color: rgb(177, 208, 181);
  width: 230px;
  margin-top: 5px;
  margin-right: 3px;
  padding: 5px 8px;
}

input[type="submit"] {
  border: 1px solid rgb(33, 108, 42);
  border-radius: 4px;
  padding: 5px;
  margin-top: 5px;
  background-color: rgb(43, 165, 57);
  color: #ffffff;
}

input[type="submit"]:hover {
  background-color: rgb(27, 187, 46);
  -webkit-box-shadow: 4px 9px 10px -6px #1e1e1e;
          box-shadow: 4px 9px 10px -6px #1e1e1e;
  -webkit-transform: translate(0px, -3px);
      -ms-transform: translate(0px, -3px);
          transform: translate(0px, -3px);
}

input[type="submit"]:active {
  -webkit-box-shadow: 0 0 0 0;
          box-shadow: 0 0 0 0;
  -webkit-transform: translate(0px, 0px);
      -ms-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
}

label {
  text-align: center;
  font-size: .95rem;
}

form.margin-fix {
  margin-bottom: 20px;
}


